.wp-block-dekode-content-generation-block {
	.components-placeholder {
		box-shadow: inset 0 0 0 1px #cc1818;
		padding: 1em;
	}

	.conversation {
		width: 100%;
		font-size: 16px;
		margin-bottom: 16px;

		& button:first-of-type {
			margin-right: 16px;
		}
	}

	.messages {
		list-style: none;
		margin: 0;
		padding: 0;
	}

	.message {
		background: rgba(0, 124, 186, 0.1);
		padding: 16px;
		margin-bottom: 16px;
		font-style: italic;
	}
}
